<template>
  <section v-if="items.length > 0">
    <div class="text-center my-8">
      <p class="text-h4 font-weight-bold">
        {{ $t("Success_Partners") }}
      </p>
      <p style="color: #00000087; font-size: 28px" class="font-weight-bold">
        {{ $t("Partners_whose_partnership") }}
      </p>
    </div>

    <v-row class="justify-center align-center">
      <v-col cols="6 " sm="3" md="2" v-for="(item, i) in items" :key="i" class="text-center">
        <div class="partenr_img" >
          <img :src="item.image" />

        </div>
        <!-- <v-img :src="item.image" class="partenr_img"></v-img> -->
      </v-col>
    </v-row>
  </section>
  <v-row v-else>
    <v-col cols="12" v-for="text in texts" :key="text">
      <v-skeleton-loader
        class="mx-auto border-0"
        type="paragraph"
      ></v-skeleton-loader>
    </v-col>
  </v-row>
</template>
<script>
import crudDataService from "../../Services/crudDataService.js";

export default {
  data() {
    return {
      items: [],
      texts: [1, 2, 3, 4,5,6],
    };
  },
  methods: {
    async sponsers() {
      let response = await crudDataService.getAll("sponsors");
      this.items = response.data;
    },
  },
  mounted() {
    this.sponsers();
  },
};
</script>
<style lang="scss">
.partenr_img {
  width: 100%;
    height: 100px;
    margin: auto;
}
.partenr_img .v-img__img--contain {
  width: 135px;
  object-fit: contain;
  margin: auto;
  right: 0 !important;
}
@media (max-width: 991.98px) {
  .partenr_img .v-img__img--contain {
    width: 115px;
  }
}
</style>
